<template>
  <div
    class="wrapper pa-4"
  >
    <h1 class="d-flex mb-2">
      <span>Hello2</span>
      <VSpacer />
    </h1>
    <div class="relative full-bleed">
      <canvas
        id="scene"
        style="border: 1px solid;max-height: 65vh;width: 100%;"
      />
    </div>
    <p>three.js </p>
    <p>1. </p>
    <p>2. </p>
    <p>3. </p>
    <p>4. </p>
    <p>5. </p>
    <p>6. </p>
    <p><a href="https://discoverthreejs.com/zh/book/first-steps/first-scene/">https://discoverthreejs.com/zh/book/first-steps/first-scene/</a></p>
  </div>
</template>

<script>
/* eslint-disable */
// 1.  
import {
  BoxGeometry,
  Color,
  Mesh,
  MeshBasicMaterial,
  PerspectiveCamera,
  Scene,
  WebGLRenderer,
} from 'three';

export default {
  data() {
    return {
      renderer: null,
      scene: null,
      camera: null,
    };
  },
  mounted() {
    this.init3();
  },
  methods: {
    async init3() {
      const container = document.querySelector('#scene');

      // 2. 
      this.scene = new Scene();
      //  
      this.scene.background = new Color('skyblue');


      // 3. 
      // Create a camera
      const fov = 35; // AKA Field of View
      const aspect = container.clientWidth / container.clientHeight;
      const near = 0.1; // the near clipping plane
      const far = 1000; // the far clipping plane
      console.log(fov, aspect, near, far, 'fov, aspect, near, far');
      this.camera = new PerspectiveCamera(fov, aspect, near, far);
      this.camera.position.set(2, 2, 10);

      // 4.
      const geometry = new BoxGeometry(1, 1, 1);
      const material = new MeshBasicMaterial({color: 0x44aa88});
      const cube = new Mesh(geometry, material);
      this.scene.add(cube);

      // 5. 
      this.renderer = new WebGLRenderer({antialias: true, canvas: container});
      //  300*150 
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      // DPR
      this.renderer.setPixelRatio(window.devicePixelRatio);
      console.log(window.devicePixelRatio, 'window.devicePixelRatio');

      // 6. 
      this.renderer.render(this.scene, this.camera);
    },
  }
}
</script>

